<template>
  <form>
    <input type="file" ref="file" style="display: none" @change="processFile($event)">
    <button type="button" @click="$refs.file.click()" class="btn btn-primary">Datei einlesen</button>
    <button class="btn btn-primary ml-2" v-on:click="exportJsonData">Export</button>
    <button class="btn btn-primary ml-2" v-on:click="fullNew">Neu</button>
  </form>
</template>

<script>
export default {
  name: "AdminViewer",
  methods: {
    readJson(pureString) {
      var editData = JSON.parse(pureString);
      this.$emit("new-data-parse", editData);
    },
    processFile(e) {
      if (e.target.files[0]) {
        var reader = new FileReader();
        //set event for async reader when finished
        
        reader.onload = z => {
          this.$refs.file.value = '';
          this.readJson(z.target.result);
          }
        reader.readAsText(e.target.files[0]);
        
      }
    },
    exportJsonData(e) {
      e.preventDefault();
      this.$emit('export-data');
    },
    fullNew(e) {
      e.preventDefault();
      var resdata = {
        id: 1,
        title: "Titel",
        explanation: "Erklärung",
        info:
          "Info",
        visible: true,
        children: []
      };
      this.$emit('overwrite-data', resdata);
    }
  }
};
</script>

