<template>
  <div class="container text-left">
    <AdminParser v-on:new-data-parse="switchModel" v-on:overwrite-data="reset" v-on:export-data="exportData"/>
    <hr>
    <div v-if="adEditData">
      <AdminViewer v-on:elem-modified="elemMod" v-bind:step="adEditData" v-bind:first=true v-bind:depth="1"/>
    </div>
  </div>
</template>

<script>
import AdminViewer from "../components/adm/AdminViewer.vue";
import AdminParser from "../components/adm/AdminParser.vue";

export default {
  name: "Admin",
  components: {
    AdminViewer,
    AdminParser
  },
  data() {
    return {
      adEditData: null,
      adEditString: ""
    };
  },
  methods: {
    exportData(){
      var str = JSON.stringify(this.adEditData);
      alert(str);
    },
    elemMod(dat) {
      if (this.adEditData.id == dat.parentId) {
        if (dat.type == "new") {
          this.adEditData.children.push(dat.elem);
        } else if (dat.type == "mod") {
          this.adEditData.title = dat.elem.title;
          this.adEditData.explanation = dat.elem.explanation;
          this.adEditData.info = dat.elem.info;
        }
      } else {
        if (!this.adEditData.children) {
          return false;
        }
        for (var child of this.adEditData.children) {
          this.recAddMod(child, this.adEditData, dat);
        }
      }
    },
    recAddMod(current, og, dat) {
      if (current.id == dat.parentId) {
        if (dat.type == "new") {
          current.children.push(dat.elem);
        } else if (dat.type == "mod") {
          current.title = dat.elem.title;
          current.explanation = dat.elem.explanation;
          current.info = dat.elem.info;
        } else if (dat.type == "del") {
          og.children = og.children.filter(child => child.id !== dat.parentId);
        }
        return true;
      } else {
        if (!current.children) {
          return false;
        }
        for (var child of current.children) {
          var found = this.recAddMod(child, current, dat);
          if (found == true) {
            break;
          }
        }
      }
      return false;
    },
    switchModel(dat){
      this.adEditData = dat;
    },
    reset(dat){
      this.adEditData = dat;
    }
  }
};
</script>

