<template>
  <div>
    <h3>{{depth}}: {{step.title}}</h3>
    <p>Explanation: {{step.explanation}}</p>
    <p>Info: {{step.info}}</p>
    <button class="btn btn-sm btn-primary" v-on:click="showNewSub">Neues Unterelement</button>
    <button class="btn btn-sm ml-2 btn-primary" v-on:click="showEditElem">Bearbeiten</button>
    <button  v-if="first==false" class="btn btn-sm ml-2 btn-danger" v-on:click="delElem">L&ouml;schen</button>
    <div v-if="newSub == true || editSelf == true">
      <form>
        <div class="form-group">
          <label for="titleI">Titel</label>
          <input
            type="text"
            id="titleI"
            class="form-control"
            placeholder="Titel eingeben"
            v-model="newE.title"
          >
        </div>
        <div class="form-group">
          <label for="expI">Erklärung (Vorschau bei Auswahl)</label>
          <input
            type="text"
            id="expI"
            class="form-control"
            placeholder="Kurze Erklärung"
            v-model="newE.explanation"
          >
        </div>
        <div class="form-group">
          <label for="infoI">Text bei Auswahl:</label>
          <textarea class="form-control" id="infoI" rows="5" v-model="newE.info"></textarea>
        </div>
        <button class="btn btn-primary" v-on:click="saveElement">Speichern</button>
        <button class="btn btn-danger ml-2" v-on:click="cancelNewElem">Abbrechen</button>
      </form>
    </div>
    <div v-for="stepc in step.children" v-bind:key="stepc.id" class="mt-5">
      <hr>
      <AdminViewer v-on:elem-modified="elemMod" v-bind:step="stepc" class="ml-5" v-bind:first=false v-bind:depth="depth+1"/>
    </div>
  </div>
</template>

<script>
import uuid from "uuid";

export default {
  name: "AdminViewer",
  props: ["step", "first","depth"],
  data() {
    return {
      newSub: false,
      editSelf: false,
      newE: {
        title: "",
        explanation: "",
        info: ""
      }
    };
  },
  methods: {
    cancelNewElem() {
      this.newSub = false;
      this.editSelf = false;
      this.newE.title = "";
      this.newE.explanation = "";
      this.newE.info = "";
    },
    showNewSub(e) {
      e.preventDefault();
      this.cancelNewElem();
      this.newSub = true;
      this.editSelf = false;
    },
    showEditElem(e) {
      e.preventDefault();
      this.newSub = false;
      this.editSelf = true;
      this.newE.title = this.step.title;
      this.newE.explanation = this.step.explanation;
      this.newE.info = this.step.info;
    },
    saveElement(e) {
      e.preventDefault();
        var ev = {
          type: "new",
          parentId: this.step.id,
          elem: {
            id: uuid.v4(),
            title: this.newE.title,
            explanation: this.newE.explanation,
            info: this.newE.info,
            visible: false,
            children: []
          }
        };
        if(this.editSelf == true){
            ev.type = "mod";
            ev.id = this.step.id;
        }
        //this.step.children.push(newElem);
        //cleanup UI
        this.cancelNewElem();
        this.$emit("elem-modified", ev);
    },
    delElem(e){
      e.preventDefault();
      var ev = {
          type: "del",
          parentId: this.step.id
      }  
      this.$emit("elem-modified", ev);
    },
    elemMod(dat){
        this.$emit('elem-modified', dat);
    }
  }
};
</script>

<style scoped>
</style>
